
import { useEffect, useRef, useState } from "react";

import useFrameSDK from "../hooks/useFrameSDK";
import ERC721TokenABI from "../abi/ERC721Token.json"
import {  DEPLOYED_FACTORY_CONTRACT_ADDRESS } from "../config";
import { ethers} from 'ethers';
export const FeedTile = ({contractEvent})  => {
    const { isSDKLoaded, context, wallet, actions } = useFrameSDK();
    const [tokenURI, setTokenURI] = useState("");
    const [isLoading, setLoading] = useState(true);
    const [error, setError] = useState(null);
  
    useEffect(() => {
      const fetchEvents = async () => {
        
        if (!isSDKLoaded) return;
  
        setLoading(true);
        setError(null);
  
        try {
          // Create a provider

          const provider = new ethers.BrowserProvider(window.ethereum);
         const contract = new ethers.Contract(contractEvent.args.location, ERC721TokenABI, provider);

          // Fetch past events
          
        // Call a function from your contract
        const data = await contract.tokenURI(0); // Replace with your function name
        console.log(data);

const base64String = data.split(',')[1];
        const decodedString = atob(base64String);
console.log("Decoded String:", decodedString);
const parsedJSON = JSON.parse(decodedString);
        setTokenURI(parsedJSON);
       
        } catch (err) {
          setError(err.message);
        } finally {
          setLoading(false);
        }
      };
  
      fetchEvents();
    }, [isSDKLoaded]);

console.log(contractEvent.args.creator)
    return (
        <div className="align-items-center ">
        <article className="card" style={{maxHeight:"500px", width:"400px", maxWidth:"90%"}}>
            <div>

                <div className="advlabel" style={{ display: "flex", flexDirection: "column", justifyContent: "center" }}>
                    <img style={{objectFit:"contain"}} src={tokenURI.image}/>
                    <p>{tokenURI.name}</p>
                    {true != "" ?
                    <a>
                        <button
                            type="button"
                            className={"btn btn-sm  btn-outline-primary btndisable advlabel"}
                            style={{ border: "none" }}
                            onClick={() => {
                                if (false) {
                                   // actions.openUrl(`https://basescan.org/nft/${publishedAddress}/1`);

                                } else {
                                    window.open(`https://basescan.org/nft/${contractEvent.args.location}/1`, "_blank");
                                }
                            }}
                        >
                            View On Basescan
                        </button>
                        </a>
                        : "Loading"}
                </div>

            </div>

        </article>
        </div>
    )
}