export function convertPNGtoSVG(base64PNG) {
  // Create a new Image object
  const img = new Image();

  // Set the src of the Image to the base64-encoded PNG
  img.src = `data:image/png;base64,${base64PNG}`;

  // Wait for the image to load
  return new Promise((resolve, reject) => {
    img.onload = () => {
      // Create a canvas element
      const canvas = document.createElement('canvas');
      canvas.width = img.width;
      canvas.height = img.height;

      // Draw the image on the canvas
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0);

      // Get the image data from the canvas
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);

      // Create an SVG element
      const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
      svg.setAttribute('width', canvas.width);
      svg.setAttribute('height', canvas.height);

      // Create a path element and set its d attribute to the image data
      const path = document.createElementNS('http://www.w3.org/2000/svg', 'path');
      path.setAttribute('d', `M0,0 L${canvas.width},0 L${canvas.width},${canvas.height} L0,${canvas.height} Z`);
      path.setAttribute('fill', `url(#image)`);

      // Create a pattern element and set its image to the canvas data
      const pattern = document.createElementNS('http://www.w3.org/2000/svg', 'pattern');
      pattern.setAttribute('id', 'image');
      pattern.setAttribute('patternUnits', 'userSpaceOnUse');
      pattern.setAttribute('width', canvas.width);
      pattern.setAttribute('height', canvas.height);
      const patternImage = document.createElementNS('http://www.w3.org/2000/svg', 'image');
      patternImage.setAttribute('width', canvas.width);
      patternImage.setAttribute('height', canvas.height);
      patternImage.setAttribute('href', canvas.toDataURL('image/png'));
      pattern.appendChild(patternImage);

      // Append the pattern and path elements to the SVG
      svg.appendChild(pattern);
      svg.appendChild(path);

      // Return the SVG element
      resolve(svg);
    };

    img.onerror = (error) => {
      reject(error);
    };
  });
}

export function svgToDataURI(svgElement) {
  // Create a new XMLSerializer object
  const serializer = new XMLSerializer();

  // Serialize the SVG element to a string
  const svgString = serializer.serializeToString(svgElement);

  // Encode the SVG string as a data URI
  const dataURI = `data:image/svg+xml;base64,${btoa(svgString)}`;

  return dataURI;
}