import { watchContractEvent, useAccount } from '@wagmi/core';
import { useEffect, useRef, useState } from "react";
import { getContractEvents } from 'viem/actions';

import useFrameSDK from "../hooks/useFrameSDK";
import ScribbleFactoryABI from "../abi/ScribbleFactory.json"
import ERC721TokenABI from "../abi/ERC721Token.json"
import {  DEPLOYED_FACTORY_CONTRACT_ADDRESS } from "../config";
import { viemWagmiClient, wagmiConfig } from '../wallet/WagmiProvider';
import { ethers} from 'ethers';
import { FeedTile } from './FeedTile';

export const Feed = () => {
    const { isSDKLoaded, context, wallet, actions } = useFrameSDK();
    const [events, setEvents] = useState([]);
    const [isLoading, setLoading] = useState(true);
    const [error, setError] = useState(null);
  
    useEffect(() => {
      const fetchEvents = async () => {
        
        if (!isSDKLoaded) return;
  
        setLoading(true);
        setError(null);
  
        try {
          // Create a provider

          const provider = new ethers.BrowserProvider(window.ethereum);
         const contract = new ethers.Contract(DEPLOYED_FACTORY_CONTRACT_ADDRESS, ScribbleFactoryABI, provider);

          // Fetch past events
          const filter = contract.filters.ScribbleCreated(); // Replace with your event name
          const pastEvents = await contract.queryFilter(filter, 26966413, 'latest'/* 'latest' */); // Adjust block range if needed
       
          setEvents(pastEvents);
        } catch (err) {
            console.log(err);
          setError(err.message);
        } finally {
         setLoading(false);
        }
      };
  
      fetchEvents();
    }, [isSDKLoaded]);

    return (
    <>
    <div  style={{overflowX:"hidden !important", maxWidth:"100%"}}>
        {isLoading?
        <>
        <image src={`/loading.gif`}/>
        <h1>Loading</h1>
      <h3>This May Take Up a Minute</h3>
      <p>Your feed is being loaded direct through your wallets RPC provider. This means theres no backend server used by scribble to make this happen</p>
        </>
        :
            <>     
             <h1>Past Doodles</h1>
             <p>currently only works on desktop not mobile</p>
            </>

    }

      <ul  style={{overflowX:"hidden !important", listStyleType:"none"}}>
        {events.map((event, index) => (
          <li key={index}>
            <FeedTile contractEvent={event}/>

          </li>
        ))}
      </ul>
    </div>
    </>
    );
}