import sdk from "@farcaster/frame-sdk";
import { useEffect, useState } from "react";

const useFrameSDK = () => {
  const [isSDKLoaded, setIsSDKLoaded] = useState(false);
  const [context, setContext] = useState();//FrameContext
  const [wallet, setWallet] = useState();
  const [actions, setActions] = useState();

  useEffect(() => {
    const load = async () => {
      setContext(await sdk.context);
     // sdk.on("primaryButtonClicked", () => sdk.actions.close());
     // await sdk.actions.setPrimaryButton({ text: "Goodbye" });
      setWallet(await sdk.wallet);
      setActions(sdk.actions);
      sdk.actions.ready({});
    };

    if (sdk && !isSDKLoaded) {
      setIsSDKLoaded(true);
      load();
    }
  }, [isSDKLoaded]);

  return { isSDKLoaded, context, wallet, actions };
};

export default useFrameSDK;